import "../assets/css/bootstrap.min.css";
import "../assets/css/style.css";
import "../assets/css/animate.css";

import footer_bg from "../assets/img/bg-5.jpg";
import JN59UlI from "../assets/img/5.png";
import twitter from "../assets/img/twitter.svg";
import discord from "../assets/img/discord.svg";
import telegram from "../assets/img/telegram.svg";
import metamask from "../assets/img/metamask-icon.webp";

import { connectWallet, getCurrentWalletConnected } from "../util/interact.js";

import { useEffect, useState } from "react";
import { HashRouter as Router, Route, Link} from 'react-router-dom';
import { useHistory } from "react-router-dom";

import { icoAddress } from '../constants/address';
import { chainId } from '../constants/address';
import api from "../util/api.js";
import $ from "jquery";
import Web3 from 'web3'

function AccountPage() {
  const [walletAddress, setWallet] = useState("Please connect the wallet.");
  const [walletConnectState, setWalletConnectState] = useState("Connect Wallet");
  const [lockedTokenAmount, setLockedTokenAmount] = useState(0);
  const [tokenLockTime, setTokenLockTime] = useState(0);
  const [tokenUnlockTime, setTokenUnlockTime] = useState(0);

  const [status, setStatus] = useState("");
  
  const [icoContract, setIcoContract] = useState("");
  let history = useHistory();

  // const rpcURL = "https://bsc-dataseed.binance.org/";
  // const rpcURL = "https://data-seed-prebsc-1-s1.binance.org:8545";
  const rpcURL = "https://api.techpay.io/";
  
  const web3 = new Web3(rpcURL);
  

  useEffect(async () => {
    const { address, status } = await getCurrentWalletConnected();
    setWallet(address);
    setStatus(status);

    addStyles();
    getContractAndShowInfo(address);
    addWalletListener();
    showWalletAddress(address);
    if (address.length == 0 ) {
      setWallet("Please connect the wallet.");
    }

  }, []);

  const getContractAndShowInfo = async (walletAddress) => {
    
    const icoAbi = require("../constants/icoAbi.json");
    window.contract = await new web3.eth.Contract(icoAbi, icoAddress);

    if (walletAddress.length > 0) {
  
      let lockTokenAmount = await window.contract.methods.lockedTokenAmountPerAddress(walletAddress).call();
      setLockedTokenAmount(Number(lockTokenAmount) / 1e18);
      
      let lockTime = await window.contract.methods.lockedTimePerAddress(walletAddress).call();
      if (lockTime != 0) {
        setTokenLockTime(transferSecondToDate(Number(lockTime)));
        setTokenUnlockTime(transferSecondToDate(Number(lockTime) + 3600*24*365));
      }
    } else {
      setLockedTokenAmount(0);
      setTokenLockTime(0);
      setTokenUnlockTime(0);
    }

    setIcoContract(window.contract);
  }

  const addStyles = () => {
    $(window).on('scroll', function() {
      var scroll = $(window).scrollTop();
  
        if (scroll >= 80) {
            $('header').addClass('nav-fixed');
        } else {
            $('header').removeClass('nav-fixed');
        }
    });

    $(window).scroll(function() {
      if ($(this).scrollTop() > 150) {
        $('.scrollup').fadeIn();
      } else {
        $('.scrollup').fadeOut();
      }
    });
    
    $(".scrollup").on('click', function (e) {
      e.preventDefault();
      $('html, body').animate({
        scrollTop: 0
      }, 600);
      return false;
    });

    //Hide Navbar Dropdown After Click On Links
    var navBar = $(".header_wrap");
    var navbarLinks = navBar.find(".navbar-collapse ul li a.nav_item");

    $.each( navbarLinks, function( i, val ) {

      var navbarLink = $(this);

        navbarLink.on('click', function () {
          navBar.find(".navbar-collapse").collapse('hide');
          $("header").removeClass("active");
        });

    });
    
    //Main navigation Active Class Add Remove
    $('.navbar-toggler').on('click', function() {
      $("header").toggleClass("active");
    });

    $(document).on("ready", function () {
      if ($(window).width() > 991) {
        $("header").removeClass("active");
      }
      $(window).on("resize", function () {
      if ($(window).width() > 991) {
          $("header").removeClass("active");
        }
      })
    })
  }

  const transferSecondToDate = (sec) => {
   
    let t = new Date(1970, 0, 1);
    t.setSeconds(sec);
    
    let date = t.getFullYear() + "/" + (t.getMonth() + 1) + "/" + t.getDate();

    return date;
  }

  const showWalletAddress = (walletAddress) => {
    if (walletAddress.length > 0) {
      let addr = walletAddress;
      addr = addr.slice(0, 5) + "...." + addr.slice(-5);
      setWalletConnectState(addr);
    }
  }

  const addWalletListener = () => {
    if (window.ethereum) {
      window.ethereum.on("accountsChanged", (accounts) => {
        if (accounts.length > 0) {
          setWallet(accounts[0]);
          getContractAndShowInfo(accounts[0]);
          let addr = accounts[0];
          addr = addr.slice(0, 5) + "...." + addr.slice(-5);
          setWalletConnectState(addr);
          setLockedTokenAmount(0);
          setTokenLockTime(0);
          setTokenUnlockTime(0);
        } else  {
          setWallet("");
          setWalletConnectState("Connect Wallet");
        }
      });
    } else {
      setStatus(
        <p>
          <a
            target="_blank"
            href={`https://metamask.io/download.html`}
            rel="noreferrer"
          >
            You must install Metamask, a virtual Ethereum wallet, in your
            browser.
          </a>
        </p>
      );
    }
  }
  

  const connectWalletFunc = async () => {
    if (window.ethereum) {
      try {
        const addressArray = await window.ethereum.request({
          method: 'eth_requestAccounts',
        })
        if (addressArray.length > 0) {
          setWallet(addressArray[0]);
          let addr = addressArray[0];
          addr = addr.slice(0, 5) + "...." + addr.slice(-5);
          setWalletConnectState(addr);
        } else {
        }
      } catch (err) {
      }
    } else {
    }
  }

  const withdraw = async () => {

    if (walletAddress == "") {
      alert("Please connect your wallet");
      return;
    }
    
    let lockTime = await icoContract.methods.lockedTimePerAddress(walletAddress).call();
    let unLockTime = Number(lockTime) + 3600 * 24 * 365;

    let curTime = Date.now() / 1e3;
  
    if (curTime > unLockTime) {
      const transactionParameters = {
        to: icoAddress,
        from: walletAddress,
        data: icoContract.methods.withdrawLockedToken(walletAddress).encodeABI(),
      };
  
      await window.ethereum.request({
        method: "eth_sendTransaction",
        params: [transactionParameters],
      }).then (function(res, err) {
        if (res) {
          alert("Withdraw successfully performed.")
        }
        console.log("Withdraw successfully performed.");
      });

    } else {
      alert("Withdraw is still now allowed");
    }

  }

  return (
    <>
    <div className="v_dark loaded" data-spy="scroll" data-offset="110" data-new-gr-c-s-check-loaded="14.1050.0" data-gr-ext-installed="">
      <div className="parallax-mirror div1-1">
        <img className="parallax-slider img1-1" src={footer_bg} data-xblocker="passed" />
      </div>
      
      <header className="header_wrap fixed-top">
        <div className="container-fluid">
          <nav className="navbar navbar-expand-lg"> 
            <a className="navbar-brand page-scroll animation animated fadeInDown" data-animation="fadeInDown" data-animation-delay="1s" style={{animationDelay: '1s', opacity: 1}}>
              <img className="logo_light fav-logo" height="50px" width="50px" src={JN59UlI} alt="logo" /> 
              <img className="logo_dark fav-logo" src={JN59UlI} alt="logo" data-xblocker="passed" style={{visibility: 'visible'}} /> 
              <strong style={{color:'white', marginLeft:'5px'}}>Uzumaki</strong>
            </a>
              <div className="space animation animated fadeInDown"><span style={{color:"white", float:"right"}}>{walletConnectState}</span></div>
            <button className="navbar-toggler green animation animated fadeInDown" height="20px" width="20px" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation" data-animation="fadeInDown" data-animation-delay="1.1s" style={{animationDelay: '1.1s', opacity: 1}}>
              â‰¡
            </button>
            <button className="navbar-toggler1 green animation animated fadeInDown" onClick={() => { connectWalletFunc() }} type="button" data-animation-delay="1.1s" style={{animationDelay: '1.1s', opacity: 1}}>
              <img className="logo_light fav-logo" height="20px" width="20px" src={metamask} alt="logo" /> 
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav m-auto">
                <li className="dropdown animation animated fadeInDown nav-item" data-animation="fadeInDown" >
                  <Link to="/" className="nav-link">Home</Link>
                </li>
                <li className="dropdown animation animated fadeInDown nav-item" data-animation="fadeInDown">
                  <Link to="/presale" className="nav-link">Presale</Link>
                </li>
                <li className="dropdown animation animated fadeInDown nav-item" data-animation="fadeInDown">
                  <Link to="/account" className="nav-link">Account</Link>
                </li>
                <li className="dropdown animation animated fadeInDown nav-item" data-animation="fadeInDown">
                  <Link to="/admin" className="nav-link">Admin</Link>
                </li>
              </ul>
              <span className="wallet-address animation animated fadeInDown" data-animation="fadeInDown">{walletConnectState}</span>
            </div> 
          </nav>
        </div>
      </header> 
        <div id="sm-walletstate" className=" animation animated fadeInDown"><span style={{color:"white"}}>{walletConnectState}</span></div>
      <section className="account-first" data-z-index="1" data-parallax="scroll" data-image-src={footer_bg}>
        <div className="container">
          <div className="row">
            <div className="col-lg-1 col-md-12 col-sm-12">
            </div>
            <div className="col-lg-10 col-md-12 col-sm-12">
              <div className="glass animation animated fadeInDown presale-second">
                <div className="text-center">
                  <strong className="account-first-text" style={{wordWrap:'break-word'}}>{walletAddress}</strong>
                  <br/>
                  <br/>
                  <span className="text-sm">Your account address</span>
                </div>
              </div>
            </div>
            <div className="col-lg-1 col-md-12 col-sm-12">
            </div>
          </div>
        </div>
      </section>
      
      <section className="" data-z-index="1" data-parallax="scroll" data-image-src={footer_bg}>
        <div className="container">
          <div className="row">
            <div className="col-lg-3 col-md-12 col-sm-12">
            </div>
            <div className="col-lg-6 col-md-12 col-sm-12">
              <div className="glass animation animated fadeInDown">
                <div className="text-center">
                  <strong className="general-text">{lockedTokenAmount} Uzumaki</strong>
                  <br/>
                  <span className="text-sm">Your Uzumaki Amount Locked</span>
                </div>
              </div>
            </div>
            <div className="col-lg-3 col-md-12 col-sm-12">
            </div>
          </div>
        </div>
      </section>

      <section className="" data-z-index="1" data-parallax="scroll" data-image-src={footer_bg}>
        <div className="container">
          <div className="row">
            <div className="col-lg-6 col-md-12 col-sm-12">
              <div className="glass animation animated fadeInDown">
                <div className="text-center">
                  <strong className="general-text">{tokenLockTime}</strong>
                  <br/>
                  <span className="text-sm">Token Lock Time</span>
                </div>
              </div>
            </div>
            <div className="col-lg-6 col-md-12 col-sm-12">
              <div className="glass animation animated fadeInDown">
                <div className="text-center">
                  <strong className="general-text">{tokenUnlockTime}</strong>
                  <br/>
                  <span className="text-sm">Token Unlock Time</span>
                </div>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-lg-2 col-md-12 col-sm-12 presale-button-container">
            </div>
            <div className="col-lg-8 col-md-12 col-sm-12 presale-button-container">
              <button id="withdraw-button" className=" animation animated fadeInDown green" onClick={() => { withdraw() }}>Withdraw</button>
            </div>
            <div className="col-lg-2 col-md-12 col-sm-12 presale-button-container">
            </div>
          </div>
        </div>
      </section>
      
      <footer>
        <div className="bottom_footer">
          <div className="container">
            <div className="row footer_img">
              <div className="col-lg-6 col-md-12 col-sm-12 footer-left">
                  <strong className="general-text">Connect with us : </strong>
              </div>
              <div className="col-lg-6 col-md-12 col-sm-12 footer-right">
                  <a href="#">
                    <img src={twitter}/>
                  </a>
                  <a href="#">
                    <img src={discord}/>
                  </a>
                  <a href="#">
                    <img src={telegram}/>
                  </a>
              </div>
            </div>
          </div>
        </div>
      </footer>
      <div style={{height: '30px'}}></div>

      <a href="#" className="scrollup green" style={{display: 'none'}}>
        â‡§
      </a>
    </div>
    </>
  );
}

export default AccountPage;
